              > -->
              <b-form-input
                v-model="email"
                :placeholder="$t('Email')"
                size="lg"
                class="saj-form-text"
                rows="8"
              />

              <small class="text-danger">{{ errors[0] }}</small>
            </validation-provider>

          </div>

          <div class="d-flex col-12 mb-3 no-gutters">

            <!-- <router-link
            class="col-12"
            :to="{
              name:
                'auth-password'}"
          >
            <button
              type="submit"
