<script>
import {
  BFormInput,
} from "bootstrap-vue"
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required } from '@validations'
import SAJToast from "@/component/saj-toastification.vue"
export default {
  components:{
    ValidationProvider,
    ValidationObserver,
    BFormInput,
    },
  data() {
    return {
      required,
      email: '',
      buttonDisabled: true,
    }
  },
  methods: {
    checkEmail(){
      if (this.email === null || this.email === ''){
        this.buttonDisabled = true
      } else {
        this.buttonDisabled = false
      }
    },
    apiSendEmail() {
      this.$refs.validateEmployee.validate().then(success => {
        if (success){
          const data = new FormData()
          data.append('email', this.email)

          this.$axios.post(`${this.$baseUrl}/password/forgot`, data).then(response => {

             const newEmail = response.data.data
              // console.log('email: ', newEmail)
              this.$router.push({ name: 'auth-password', params: { getEmail: newEmail} })

          }).catch(() =>{
            this.$toast(
            {
              component: SAJToast,
              props: {
                title: `${this.$t('Please sign up, your email does not exist')}.`,
                icon: "AlertCircleIcon",
                iconColor: 'white',
                iconBg: '#e80202',
                titleColor: "#000",
              },
            },
            {
              position: "top-right",
              type: 'info',
            },
          )
          })
        }
      })
    },
  },
}

</script>
